// General Styles
body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
}

// Container
.container {
    width: 50%;
    margin: 50px auto;
    padding: 20px;
    background: white;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

// Headings
h2 {
    text-align: center;
    color: #333;
}

// Form Styles
.form {
    display: flex;
    flex-direction: column;

    label {
        margin-top: 10px;
        font-weight: bold;
    }

    input,
    select {
        width: 100%;
        padding: 8px;
        margin-top: 5px;
        border: 1px solid #ccc;
        border-radius: 4px;
    }
}

// Buttons
.btn {
    margin-top: 15px;
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.3s;

    &:hover {
        background-color: #0056b3;
    }
}

// Success & Error Messages
.success-message {
    color: green;
    font-weight: bold;
    text-align: center;
}

.error-message {
    color: red;
    font-weight: bold;
    text-align: center;
}

// Stock Inputs
#stockInputs {
    label {
        display: block;
        margin-top: 10px;
        font-size: 14px;
    }
}
